<template>
	<AppHeader />

	<AppFilters :active-filter="activeFilter" @setFilter="setFilter"/>

	<main class="app-main">
		<AppTodoList :tasks="filteredTasks" @toggleTask="toggleTask" @deleteTask="deleteTask"/>

		<AppAddTodo @addTask="addTask"/>
	</main>

	<AppFooter :stats="stats"/>
</template>

<script lang="ts">
	import {defineComponent} from 'vue'
	import AppHeader from '@/components/AppHeader.vue'
	import AppFilters from "@/components/AppFilters.vue";
	import AppTodoList from "@/components/AppTodoList.vue";
	import AppAddTodo from "@/components/AppAddTodo.vue";
	import AppFooter from "@/components/AppFooter.vue";
	import {Task} from "@/Types/Task";
	import {Filter} from "@/Types/Filter";
	import {Stats} from "@/Types/Stats";

	interface State {
		tasks: Task[],
		activeFilter: Filter
	}

	export default defineComponent({
		components: {
			AppHeader,
			AppAddTodo,
			AppTodoList,
			AppFilters,
			AppFooter
		},
		data(): State {
			return {
				tasks: [
					{
						id: 1,
						text: "Hello",
						completed: true,
					},
					{
						id: 2,
						text: "Hi",
						completed: false,
					},
					{
						id: 3,
						text: "Hay",
						completed: false,
					},
					{
						id: 4,
						text: "Welcome",
						completed: true,
					},
				],
				activeFilter: 'All'
			};
		},
		computed: {
			filteredTasks(): Task[] {
				switch (this.activeFilter) {
					case 'Active':
						return this.activeTasks
					case 'Done':
						return this.DoneTasks
					case 'All':
					default:
						return this.tasks
				}
			},
			stats(): Stats {
				return {
					active: this.activeTasks.length,
					done: this.DoneTasks.length
				}
			},
			activeTasks(): Task[] {
				return this.tasks.filter(task => !task.completed)
			},
			DoneTasks(): Task[] {
				return this.tasks.filter(task => task.completed)
			}
		},
		methods: {
			toggleTask(id: number) {
				const targetTask = this.tasks.find((task: Task ) => task.id === id)

				if (targetTask) {
					targetTask.completed = !targetTask.completed
				}
			},
			deleteTask(id: number) {
				this.tasks = this.tasks.filter((task: Task) => task.id !== id)
			},
			addTask(task: Task) {
				this.tasks.push(task)
			},
			setFilter(filter: Filter) {
				this.activeFilter = filter
			}
		}
	})
</script>
