<template>
  <li class="todo-item" :class="{ 'todo-item--done': task.completed }" @click="toggleTask">
    <div class="todo-item__status">
      <i class="bi bi-check2"></i>
    </div>
    <span class="todo-item__text">
      {{ task.text }}
    </span>
    <button class="todo-item__remove-button" @click.stop="deleteTask">
      <i class="bi bi-trash3"></i>
    </button>
  </li>
</template>

<script lang="ts">
import { Task } from "@/Types/Task";
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    task: {
      type: Object as PropType<Task>,
      required: true,
    },
  },
	methods: {
		toggleTask() {
			this.$emit('toggleTask', this.task.id)
		},
		deleteTask() {
			this.$emit('deleteTask', this.task.id)
		}
	},
	emits: {
		toggleTask: (id: number) => Number.isInteger(id),
		deleteTask: (id: number) => Number.isInteger(id)
	}
});
</script>
