<template>
  <section class="add-todo">
    <form @submit.prevent="addTask" v-if="isFormVisible" class="add-todo__form">
      <button @click="closeForm" class="close-button" type="button">
        <i class="bi bi-x"></i>
      </button>
      <div class="text-input text-input--focus">
        <input v-model="taskText" class="input" />
      </div>
      <button class="button button--filled">Add task</button>
    </form>
    <button v-else @click="showForm" class="add-todo__show-form-button">
      <i class="bi bi-plus-lg"></i>
    </button>
  </section>
</template>

<script lang="ts">
import {defineComponent} from "vue";
import {Task} from "@/Types/Task";

interface State {
	isFormVisible: boolean,
	taskText: string
}
export default defineComponent ({
	data(): State {
		return {
			isFormVisible: false,
			taskText: ''
		}
	},
	methods: {
		showForm() {
			this.isFormVisible = true
		},
		closeForm() {
			this.isFormVisible = false
		},

		addTask() {
			this.$emit('addTask', {
				id: Date.now(),
				text: this.taskText,
				completed: false
			})
			this.taskText = ''
		}
	},
	emits: {
		addTask: (task: Task) => task
	}
})
</script>
